:root {
	--accent: #757575;
	--light: #fff;
}

body,html {
	height: 100%;
	width: 100%;
}

body {
	margin: 0;
	padding: 0;
	background-image: url(/static/media/bg_d.9cefeeb5.jpg);
	background-size: cover;
	background-attachment: fixed;
	background-position: center center;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-ms-align-items: center;
	-webkit-align-items: center;
	        align-items: center;
}

#root {
	max-width: 576px;
	width: 100%;
	padding: 10px;
}
