.app {
	color: #fff;
	color: var(--light);
}

.app-main {
	padding: 20px;
	border-radius: 20px;
	background-color: #fff;
	background-color: var(--light);
	color: #000;

}
.app-main__content {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-ms-align-items: center;
	-webkit-align-items: center;
	        align-items: center;
	font-family: Lora;
}

.app-main__content-main {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	-ms-align-items: center;
	-webkit-align-items: center;
	        align-items: center;
}

.bold {
	font-weight: bold;
}
.grey {
	color: #757575;
	color: var(--accent);
}


.temp-value {
	font-size: 18px;
	color: var(--accent);
}

.sky {
	margin-top: 5px;
	font-size: 15px;
	font-weight: 500;
	color: #757575;
}

.sky-img {
	width: 120px;
	height: 120px;
}
.searchLocations {
	margin-bottom: 10px;
	position: relative;
}

.searchLocations-input {
	padding: 7px 15px;
	box-sizing: border-box;
	width: 100%;
	font-size: 16px;
	color: var(--accent);
}

.searchLocations-input:focus {
	outline: none;
}

.searchLocations-label {
	display: block;
	margin-bottom: 10px;
	font-size: 17px;
	font-weight: bold;
	font-family: 'Merriweather';
}

.searchLocations-list {
	list-style: none;
	background-color: var(--light);
	color: #000;
	padding: 10px;
	margin-top: 0px;
	max-height: 150px;
	overflow-y: auto;
	font-family: Lora;
	position: absolute;
	width: 100%;
	max-width: 576px;
	box-sizing: border-box;

}

.searchLocations-list__item {
	padding: 10px;
}
